
:root {
    --weather-panel-width: 60%;
}

.weather-container {
    width: 60%;
    width: var(--weather-panel-width);
    height: 100%;
    float: right;
}
.weather-attrib {
    background-color: orange;
}
.cond-desc-container {
    position: relative;
    width: 100%;
}
.weather-attrib#conditions, .weather-attrib#description {
    text-align: center;
}
.weather-attrib#conditions {
    position: absolute;
    float: left;
    width: calc(60% * 0.75);
    width: calc(var(--weather-panel-width) * 0.75);
    top: 0px;
}
.weather-attrib#icon {
    position: absolute;
    float: right;
    width: calc(60% * 0.35);
    width: calc(var(--weather-panel-width) * 0.35);
    top: 0px;
    right: 0px;
}
.weather-attrib#details {
    position: absolute;
    top: 50%;
}
.weather-attrib#location {
    width: 60%;
    width: var(--weather-panel-width);
    position: absolute;
    height: 5%;
    bottom: 40%;
}
.weather-icon img{
    -webkit-filter: invert(100%);
            filter: invert(100%);
}
:root {
    --time-panel-width: 40%;
}

.time-container {
    width: 40%;
    width: var(--time-panel-width);
    height: 100%;
    float: left;

    text-align: center;
}
.display-container {
    width: 40%;
    width: var(--time-panel-width);
    position: absolute;
    top: calc(var(--content-height) / 2);
    color: white;
}
.time-display {
    background-color: #333333;
    font-size: 6em;
}
.date-display {
    background-color: #3f3f3f;
    font-size: 1.5em;
}
:root {
    --background-color: #1f1f1f;
    --content-height: 80%;
}

html, body, div#root{
    margin: 0;
    background-color: #1f1f1f;
    background-color: var(--background-color);
    height: 100% !important;
    width: 100% !important;
    font-family: Arial, Helvetica, sans-serif;
}

.content {
    position: relative;
    height: 80%;
    height: var(--content-height);
    display: block;
}

div#large-font {
    font-size: 9em;
}
